<template>
    <div class="shoppingCart">
    <!--  -->
    <div class="cart-header">
      <div class="cart-header-content">
        <p>
          <i class="el-icon-shopping-cart-full" style="color:#ff6700; font-weight: 600;"></i>
          
        </p>
        <span></span>
      </div>
    </div>
    <!-- END -->

    <!--  -->
    <div class="content" v-if="getShoppingCart.length>0">
      <ul>
        <!--  -->
        <li class="header">
          <div class="pro-check">
            <el-checkbox v-model="isAllCheck"></el-checkbox>
          </div>
          <div class="pro-img"></div>
          <div class="pro-name"></div>
          <div class="pro-price"></div>
          <div class="pro-num"></div>
          <div class="pro-total"></div>
          <div class="pro-action"></div>
        </li>
        <!-- END -->

        <!--  -->
        <li class="product-list" v-for="(item,index) in getShoppingCart" :key="item.id">
          <div class="pro-check">
            <el-checkbox :value="item.check" @change="checkChange($event,index)"></el-checkbox>
          </div>
          <div class="pro-img">
            <router-link :to="{ path: '/goods/details', query: {productID:item.productID} }">
              <img :src="$target + item.productImg" />
            </router-link>
          </div>
          <div class="pro-name">
            <router-link
              :to="{ path: '/goods/details', query: {productID:item.productID} }"
            >{{item.productName}}</router-link>
          </div>
          <div class="pro-price">{{item.price}}</div>
          <div class="pro-num">
            <el-input-number
              size="small"
              :value="item.num"
              @change="handleChange($event,index,item.productID)"
              :min="1"
              :max="item.maxNum"
            ></el-input-number>
          </div>
          <div class="pro-total pro-total-in">{{item.price*item.num}}</div>
          <div class="pro-action">
            <el-popover placement="right">
              <p></p>
              <div style="text-align: right; margin: 10px 0 0">
                <el-button
                  type="primary"
                  size="mini"
                  @click="deleteItem($event,item.id,item.productID)"
                ></el-button>
              </div>
              <i class="el-icon-error" slot="reference" style="font-size: 18px;"></i>
            </el-popover>
          </div>
        </li>
        <!-- END -->
      </ul>
      <div style="height:20px;background-color: #f5f5f5"></div>
      <!--  -->
      <div class="cart-bar">
        <div class="cart-bar-left">
          <span>
            <router-link to="/goods"></router-link>
          </span>
          <span class="sep">|</span>
          <span class="cart-total">
            
            <span class="cart-total-num">{{getNum}}</span> 
            <span class="cart-total-num">{{getCheckNum}}</span> 
          </span>
        </div>
        <div class="cart-bar-right">
          <span>
            <span class="total-price-title"></span>
            <span class="total-price">{{getTotalPrice}}</span>
          </span>
          <router-link :to="getCheckNum > 0 ? '/confirmOrder' : ''">
            <div :class="getCheckNum > 0 ? 'btn-primary' : 'btn-primary-disabled'"></div>
          </router-link>
        </div>
      </div>
      <!-- END -->
    </div>
    <!-- END -->

    <!--  -->
    <div v-else class="cart-empty">
      <div class="empty">
        <h2></h2>
        <p></p>
      </div>
    </div>
    <!-- END -->
  </div>
      
    
</template>

<script>
import {mapActions} from 'vuex'
        import { mapGetters } from "vuex";
        
export default {
    
    data() {
        return {
            
        };
    },
    mounted() {
       
    },
    methods: {
          ...mapActions(["updateShoppingCart", "deleteShoppingCart", "checkAll"]),
          handleChange(val,key,productID){
              this.updateShoppingCart({key:key,prop:'check',val:true,id:productID});
              // 
          },
          checkChange(val,key){
              this.updateShoppingCart({key:key,prop:'check',val:val})
              // 
          }

        
    },
    computed:{
        ...mapGetters(['getShoppingCart', "getCheckNum",
      "getTotalPrice",
      "getNum"]),
      isAllCheck:{
          get(){
              return this.$store.getters.getIsAllcheck
          },
          set(val){
              this.checkAll(val)
          }
      }
    }
};
</script>

<style>
.shoppingCart {
  background-color: #f5f5f5;
  padding-bottom: 20px;
}
/* CSS */
.shoppingCart .cart-header {
  height: 64px;
  border-bottom: 2px solid #ff6700;
  background-color: #fff;
  margin-bottom: 20px;
  padding-bottom: 20px;
  
}
.shoppingCart .cart-header .cart-header-content {
  width: 1225px;
  margin: 0 auto;
}
.shoppingCart .cart-header p {
  font-size: 28px;
  line-height: 58px;
  float: left;
  font-weight: normal;
  color: #424242;
}
.shoppingCart .cart-header span {
  color: #757575;
  font-size: 12px;
  float: left;
  height: 20px;
  line-height: 20px;
  margin-top: 30px;
  margin-left: 15px;
  padding-top: 30px;
}
/* -------------- */

</style>